import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class UserService {

  constructor(private _http:Http) { }

  GetUser(){
    let url = '/api/user';
    return this._http.get(url).map(res => res.json());
  }

}



